.cards {
    padding: 3rem;
    background: #fff;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  

  
  .cards__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 24px;
    justify-content: center;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    flex-shrink: 1;
    min-width: 300px;

    flex-wrap: wrap;
    margin: .25rem 1rem;
    border-radius: 10px;
  }
  
  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;

    transition: all 0.2s linear;
  }
  
  .cards__item__img:hover {
    transform: scale(1.1);
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  

  
  @media only screen and (max-width: 800px) {
    .cards {
      padding: .5rem
    }
    .cards__item {
      margin-bottom: 2rem;
      max-width: 450px;
    }
  }


  
  @media only screen and (max-width: 500px) {
    .cards__item {
      max-width: none;

    }
    .cards {
      padding: 0;
      padding-top: .25em;
    }

  }

  @media only screen and (max-width: 400px) {
    .cards__item {
      max-width: none;
      min-width: initial;
    }


  }

  @media only screen and (max-width: 300px) {
    .cards__item {
      max-width: none;
      min-width: initial;
    }


  }