 padding: .5em;
 padding-top: 0;
 margin: 1.5em;
 background-color: #FFF;
 box-shadow: 0 0px 30px rgba(56, 125, 255, 0.2);
 -webkit-filter: drop-shadow(0 6px 10px rgba(56, 125, 255, 0.017));
 filter: drop-shadow(0 6px 10px rgba(56, 125, 255, 0.017));

}
