:root{
    --primary: #fff;

}

.btn {
    margin: .5em;
    margin-bottom: 0;
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--primary:hover {
    background-color: #242424;
    color: #e0e1dd;
    border: 1px solid #242424;
}

.btn--selected {
    background-color: #242424;
    color: #e0e1dd;
    border: 1px solid #242424;
}

.btn--selected:hover {
    background-color: #454444;
    color: #e0e1dd;
    border: 1px solid #e0e1dd;
}

.btn--outline {
    background-color: transparent;
    color: #e0e1dd;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}
.btn--small {
    padding: 4px 10px;
    font-size: 12px;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color: #0d1b2a;
    transition: all 0.3s ease-out;
}

.btn--primary:hover {
    background-color: #0d1b2a;
    transition: all 0.3s ease-out;
    color: #fff;
}