* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
  }
  body {
    background: linear-gradient(180deg, #242424  0%, rgb(27, 38, 59) 100%);
  }
  /* pallette
   #0d1b2a
   #1b263b
   #415a77
   #778da9
   #e0e1dd
  
