  .resources,
  .home,
  .projects,
  .aboutus,
  .contact,
  .terms,
  .privacy{
    padding: 1em ;
    min-height: 60vh;
    margin: 1em 10%;
    width: 80vw;
    font-size: 1rem;
    border: 2px solid #898686;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    background-color: #e0e1dd;
  }

  .contact-text-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    margin: 10px;

  }

  .resources{
    background-color: #e0e1dd;
  }
  .resources-header-statement {
    text-align: center;
  }
  .resources-btn-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .content-header {
    text-align: center;
    margin-bottom: 1em;
  }
  .content-header-2 {
    text-align: center;
    margin-bottom: 1em;
  }

  .main-text-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 10px;
  }
  .main-text {
    margin: 20px 0px;
  }
  .main-text-compact {
    margin: 5px 0px;
  }

  .main-help-container {
    padding: 10px;
    margin-top: 10px;
    background: linear-gradient(90deg, rgb(230, 228, 228) 0%, rgb(128, 127, 127) 100%);
  }
  .help-list {
    margin-left: 30px;
  }

  .main-img-contact {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-width: 95vw;
    height: 10rem;

  }

