  .list-wrapper {
    margin-left: .5em;
  }

  .cause-link-list {
    margin: 20px;
  }

  .three-way-left {
    display: flex;
    flex-direction: row;
    margin: 1em;

  }
  .three-way-right {
    display: flex;
    flex-direction: row-reverse;
    margin: 1em;
  }

  .three-way-text-wrapper {
    max-width: 335px;

  }
  .three-way-img {
    margin-top: 1em;
    flex-grow: 2;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-width: 120px;
    max-width: 335px;
    margin-right: 1.5%;
  }

  @media only screen and (max-width: 660px) {
    .three-way-img {
      display: none;
    }
    .content-header-2 {
      text-align: center;
      margin-bottom: 1em;
    }
  
    .main-text-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 10px;
    }
  }

  .three-way-img-spacer {
    flex-grow: 1;
    flex-shrink: 1;
  }

  .content-divider {
    min-width: 100%;
    min-height: 1px;
    border-bottom: 1px solid #242424;
  }
  
  .main-contact-link-wrapper{
    margin: auto;
    margin-left: 1em;
  }

  .link-list-item {
    margin: 10px;
    word-wrap:break-word;
  }

  .contact-links {
    outline: none;
    text-decoration: none;
    position: relative;
    font-size: 1.5em;
    color: #9e9ba4;
    display: inline-block;
    text-transform: uppercase;
    overflow: hidden;
    line-height: 1;
    top: 2px ;

  }
  .contact-links:hover {
    color:rgb(61, 54, 54);
    text-decoration: underline;
    text-transform: uppercase;
    overflow: hidden;
    line-height: 1;
    color: #b2b0a9;
  }
  .contact-links::after{
    content: '';
    position: absolute;
    height: 16px;
    width: 100%;
    top: 50%;
    margin-top: -8px;
    right: 0;
    background: #f9f9f9;
    transform: translate3d(-100%,0,0);
    transition: transform 0.4s;
    transition-timing-function: cubic-bezier(0.7,0,0.3,1);
  }

  .contact-links:hover::after {
    transform: translate3d(100%,0,0);
  }

  .contact-links::before{
      content: attr(data-letters);
      position: absolute;
      z-index: 2;
      overflow: hidden;
      color: #424242;
      white-space: nowrap;
      width: 0%;
      transition: width 0.4s 0.3s;
  }
    .contact-links:hover::before {
      width: 100%;
  }

  .form-area {
    margin-left: 1em;
    margin-right: 1em;
  }
  .btn-send {
    display: block;
    padding: 5px 10px;
    cursor: pointer;
  }

  .content-spacer {
    min-height: 2em;
  }
  .content-container {
    background-color: rgba(255, 255, 255, 0.801);
    margin: auto;
    padding: 1em;
    max-width: 90vw;
    text-align: center;
    font-size: 1rem;
    border: 2px solid #898686;

  }
  .content-inner-wrapper {
    text-align: left;
  }

  .credits {
    margin: 2em;
  }
  
  .form-input {
    padding: 5px;
  }

  .form-labels {
    padding: 5px;
  }

  .success-msg {
    color: #58c63a;
  }

  .error-msg {
    color: #d01616;
  }
  .dynamic__graphic {
    width: 30%;
    border-right: 1px solid #0d1b2a;
    padding: 1em;
  }

  .dynamic__text {
    width: 70%;
    padding: 1em;

  }
  .dynamic__container {
    border: 1px solid #0d1b2a;
    padding: 1em;
    width: 100%;
    min-height: 5em;
    margin: 1em;

  }


