
.hero-container {
    padding-top: 2em;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    background: linear-gradient(180deg, #242424  0%, rgb(27, 38, 59) 100%);

}

.hero-container > h1 {
    color: #e0e1dd;
    font-size: 100px;
    margin-top: -30px;
}

.hero-container > p {
    color: #0d1b2a;
    font-size: 32px;
    margin-top: 8px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding: 1em ;
}

.hero-btns {
    margin-top: 32px ;

}

.hero-btns .btn{
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}

@media  screen and (max-width: 960px) {
    .hero-container {
        padding-top: 3em;
    }
    .hero-container > h1 {
        font-size: 70px;

        
    }
    .hero-container > p {
        font-size: 30px;
        text-align: center;
        padding: 1em ;
    }
    
}

@media  screen and (max-width: 720px) {
    .hero-container {
        padding-top: 2em;
    }
    .hero-container > h1 {
        font-size: 50px;
    }
    .hero-container > p {
        font-size: 30px;
        text-align: center;
        padding: 1em ;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
    }
    .btn {
        width: 100%;
    }
    
}

@media screen and (max-width: 500px){
    .hero-container > h1 {
        display: initial;
        font-size: 2em;
    }
}

@media screen and (max-width: 300px){
    .hero-container > h1 {
        display: initial;
        font-size: 1em;
    }
}



